import { TextSharedDialogComponent } from './../text-shared-dialog/text-shared-dialog.component';
import { Router } from '@angular/router';
import { TestComponent } from './../../test/test.component';
import { SelectionModel } from '@angular/cdk/collections';
import { MatDialog } from '@angular/material/dialog';
import { TextsService } from './../texts.service';
import { Component, OnInit, ViewChild } from '@angular/core';
import { MatPaginator, MatTableDataSource } from '@angular/material';

@Component({
  selector: 'app-text-historic',
  templateUrl: './text-historic.component.html',
  styleUrls: ['./text-historic.component.css']
})
export class TextHistoricComponent implements OnInit {

  constructor(private textsService: TextsService,
    private router: Router, 
    private dialog: MatDialog
  ) { }

  ELEMENT_DATA:  any;
  displayedColumns: string[] = [ 'select', 'created', 'name', 'description', 'text', 'option'];
  dataSource: any;
  selection = new SelectionModel<any>(true, []);

  @ViewChild(MatPaginator) paginator: MatPaginator;

  applyFilter(filterValue: string) {
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }

  ngOnInit() {
    console.log("Get All texts")
    this.getAllTextsHistoric();
  }

  getAllTextsHistoric() {
    this.textsService.getAllTextsHistoric().subscribe(
      (data: any) => {this.ELEMENT_DATA = [...data]; 
                  this.dataSource = new MatTableDataSource<any>(this.ELEMENT_DATA);
                  this.dataSource.paginator = this.paginator;                  
      },
      error => {console.log("Erro ao buscar dados: ", error.error), alert(error.error)}
    );   
  }

  /** Whether the number of selected elements matches the total number of rows. */
  isAllSelected() {
    const numSelected = this.selection.selected.length;
    const numRows = this.dataSource.data.length;
    return numSelected === numRows;
  }

  /** Selects all rows if they are not all selected; otherwise clear selection. */
  masterToggle() {
    this.isAllSelected() ?
    this.selection.clear() :
    this.dataSource.data.forEach(row => this.selection.select(row));
  }

  isMultiSelected() {
    return (this.selection.selected.length > 1) ? true : false;
  }

  isOnlyOneSelected(id) {
    return ((this.selection.selected.length == 1) && this.selection.selected[0].id==id ) ? false : true;
  }

  openEdit() {
    console.log(this.selection.selected[0])
    let textHistoric = this.selection.selected[0];
    let data = {
      editMode: true,
      idText: textHistoric.id,
      content: textHistoric.text,
      filter: [],
      dataFilter: [],
      lastEditions: textHistoric.changesInText
    };
    this.textsService.updateData(data)
    this.router.navigate(['/texts/text-processed', textHistoric.id])
  }

  getUserIdToEmails() {
    let userIds = []
    const dialogRef = this.dialog.open(TextSharedDialogComponent, { width: '80%', disableClose: true });
    dialogRef
      .afterClosed()
        .subscribe(result => {
            userIds = result
            console.log("userIds:", userIds,result)
        });
    return userIds;
  }
  
  postSharedTextSelected() {
    const dialogRef = this.dialog.open(TextSharedDialogComponent, { width: '80%', disableClose: true });
    dialogRef
      .afterClosed()
        .subscribe(result => {
          if(result.length) {
            if(this.selection.selected.length){
              this.selection.selected.forEach(element =>{
                let idUser = element.user;
                let textId = element.id;
                let sharedUser:any = result;
  
                let data = {
                  onwerUser: idUser,
                  sharedUser: '',
                  historyChangesText: textId
                }

                for(let user of sharedUser){
                  data.sharedUser = user
                  this.textsService.postSharedText(data).subscribe(response =>{
                    console.log("Enviar email selected: ", response)
                    //enviar email aqui
                  })
                }
            });
          }
        }
      });
    }       
  
  sendEmail() {
    let data = {
      subject: 'Um texto foi compartilhado',
      message: 'Um texto foi compartilhado',
      recipient_list: ['arley.sribeiro@gmail.com',]
    }
    this.textsService.postSendEmail(data).subscribe( response => {
      console.log(response)
    });
  }

  redirectTo(item) {
    console.log(item)
    this.router.navigate(['texts/text-view-corrections/', item.id])
  }

  deleteSelected() {
    let length = this.selection.selected.length
    let title = length > 1 ? 'Deseja excluir os textos selecinados do hist√≥rico de revis√µes' : 'Deseja excluir o texto selecionado do hist√≥rico de revis√µes';
    let content = 'Ap√≥s essa opera√ß√£o ' + length +  (length > 1 ? ' registros ser√£o exclu√≠dos.' : 'registro ser√° exclu√≠do.');
    const dialogRef = this.dialog.open(TestComponent, {data: {title: title, content: content, buttonConfirm: 'Sim', buttonCancel: 'N√£o'}});

    dialogRef.afterClosed().subscribe(result => {
      console.log(`Dialog result: ${result}`);
      if(result) {       
        if(length) {
          this.selection.selected.forEach((item, index) => {        
            this.textsService.deleteHistoricText(item.id).subscribe(response=>{
              console.log("Foi deletado: ", item.id)
              if(index == length-1){
                this.getAllTextsHistoric();   
                this.selection.clear()
              }
            })
          })       
        }
      }
    });
  }
}
