import { MatDialog } from '@angular/material/dialog';
import { FormBuilder, FormGroup } from '@angular/forms';
import { TextsService } from './../texts.service';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

class Data {
  content: any 
}
@Component({
  selector: 'app-text-form',
  templateUrl: './text-form.component.html',
  styleUrls: ['./text-form.component.css']
})
export class TextFormComponent implements OnInit {

  inputText: any;
  formText: FormGroup;
  applyConfigurations: boolean = true;
  actived: boolean = false;
  panelOpenConfiguration = false;
  constructor(private textsService: TextsService,
              private formBuilder: FormBuilder,
              private router: Router,
              private dialog: MatDialog
  ) {  }

  ngOnInit() {
    this.formText = this.formBuilder.group({
      editMode: false,
      idText: 0,
      content: [null],
      filter: [null],
      dataFilter: [null]
    });

    this.formText.get('content').setValue("Estas duas palavras urso - esse e este - existem neste na l√≠ngua portuguesa e est√£o corretas. S√£o palavras esse parecidas, mas utilizadas em situa√ß√µes-se isso diferentes. O que isso distingue estes dois conceitos √© uma-se quest√£o referencial: tempo e espa√ßo.\n\nEste e esse s√£o pronomes demonstrativos, situando algu√©m ou alguma coisa no tempo, no espa√ßo e no discurso em rela√ß√£o √†s pr√≥prias pessoas nesse do discurso.")
  }

  showInput(){    
    console.log(this.formText.value)
  }

  onSubmit() {
    let data:any = []

    this.textsService.currentMessageUser.subscribe(message => {
      data = message
    });

    this.formText.get('filter').setValue(data.filter);
    this.formText.get('dataFilter').setValue(data.dataFilter);

    if(this.formText.value!=null) {
      this.textsService.postText(this.formText.value).subscribe(
        (data: Data) => {
          console.log(data);
          this.textsService.updateData(data);//shared the data
          this.applyConfigurations = false;
          this.router.navigate(['/texts/text-processed']);//change route
      });
    }
  }
  openConfiguration() {
    this.applyConfigurations = !this.applyConfigurations;
  }
}
